.bg-primary {
  background-color: #E2A22F !important;
}

.text-primary {
  color: #E2A22F !important;
}

.text-secondary {
  color: white !important;
}

.bg-primary {
  background-color: #E2A22F !important;
}

.border-primary {
  border-color: #E2A22F !important;
}

.btn-primary {
  border-color: #E2A22F !important;
}

* {
  box-sizing: border-box;
}

body {
  line-height: 1.15 !important;
}

a {
  text-decoration: none;
  color: inherit !important;
}

.portfolio-grid {
  display: grid;
  grid-gap: 0.3rem;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
}

.btn:hover {
  background: #E2A22F !important;
  color: white !important;
  transition: all 0.3s ease-in-out;
}

.hero .fa, .hero .fas, .hero .fad {
  font-size: 5rem;
}

.skill .after {
  position: relative;
}
.skill .after::after {
  position: absolute;
  top: 0;
  right: 0;
  content: attr(name) "";
}
.skill .progress {
  background: none !important;
}
.skill .progress .progress-bar {
  background: #E2A22F;
}

.qualification .list-group-item {
  padding-left: 1.3rem;
}

.services .card {
  border-radius: 0;
  border-top-right-radius: 1.5rem;
  box-shadow: 0 0 4px white;
}
.services .card:hover {
  background: #E2A22F !important;
  color: white !important;
  transition: all 0.3s ease-in-out;
}
.services .card:hover i {
  background: white !important;
  color: #E2A22F;
}
.services .card .card-header {
  margin-top: -2.5rem;
  border: none;
}
.services .card i {
  border-radius: 0.5rem;
  box-shadow: 0 0 5px white;
}
@media (max-width: 768px) {
  .services .card {
    margin-bottom: 3rem;
  }
}

.portfolio {
  display: grid;
  grid-gap: 0.3rem;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
}

.testimonial {
  background: #0a0132;
}
.testimonial .img-fluid {
  width: 8rem;
  height: 8rem;
  border-radius: 50%;
}
@media (max-width: 768px) {
  .testimonial .text-sm-center {
    text-align: center !important;
  }
}

.blog {
  background: #06011e;
}

.contact .form-control {
  border-radius: 0 !important;
  font-size: 0.875rem;
  font-weight: 600;
  background: transparent;
  color: #fff;
  border: 0.05rem solid white;
}
.contact .btn-lg:hover {
  background: white !important;
  color: #E2A22F !important;
  transition: all 0.3s ease-in-out;
}
@media (max-width: 500px) {
  .contact .address-line {
    text-align: center;
  }
  .contact .address-line .d-flex {
    text-align: left;
    align-items: flex-start;
    justify-content: center;
  }
}
.contact .address-line .me-2 {
  width: 2.5rem;
  height: 2.5rem;
  display: grid !important;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  background: #E2A22F;
}
.contact .address-line .fa {
  font-size: 1.5rem;
}

.footer .social .d-flex {
  width: 2rem;
  height: 2rem;
  align-items: center;
  justify-content: center;
}
.footer .social .d-flex .fa {
  font-size: 1.3rem;
}