@mixin font600{
    font-weight: 600;
}

@mixin  hoverEffects() {
    background: $primary !important;
    color: $secondary !important;
    transition: all 0.3s ease-in-out;
}
@mixin  hoverEffect() {
    background: $secondary !important;
